body {
  font-size: 14px;
}

.go-to-profile {
  margin-left: 10px;
  padding: 0px;
  min-width: auto;
  width: 30px;
  height: 30px;
}

.logo {
  margin-left: -62px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #3d3b3b;
}

.candidate-status-choices {
  cursor: pointer;
  padding: 2px 4px;
  border-radius: 10px;
  display: inline-block;
}

.candidate-status-choices:hover {
  background-color: #dfdfdf;
}

.candidate-status-choices .circle {
  width: 12px;
  height: 12px;
  display: inline-block;
  border-radius: 10px;
  border: 1px solid #dfdfdf;
  margin-right: 10px;
}

.type-item {
  background-color: #dfdfdf;
  margin-right: 5px;
  padding: 4px 8px;
  display: flex;
  cursor: pointer;
}

.evaluated-indicator {
  width: 28px;
  height: 28px;
  cursor: pointer;
  vertical-align: inherit !important;
  position: relative;
}

.rating-indicator {
  border-radius: 100%;
  width: 20px;
  height: 20px;
  text-align: center;
  color: white;
  cursor: pointer;
  display: inline-block;
  vertical-align: super;
  position: absolute;
  left: 42px;
  top: 5px;
}

.rating-indicator.good {
  background-color: green;
}

.rating-indicator.fine {
  background-color: yellow;
  color: inherit;
}

.rating-indicator.bad {
  background-color: red;
}

.react-calendar {
  width: 450px;
  height: 400px;
  font-size: 20px;
  background: none !important;
  border: none !important;
}

.react-calendar button {
  font-size: 20px;
}

.rbc-event {
  background-color: rgb(47, 153, 218) !important;
  color: rgb(255, 255, 255) !important;
  border-color: rgb(38, 146, 212) !important;
}

.rbc-event-content {
  white-space: normal !important;
}

.ai-yellow {
  background-color: rgb(253 224 71);
}

.ai-yellow:hover {
  background-color: rgb(247, 206, 1);
}

.ai-red {
  background-color: rgb(252 165 165);
}

.ai-red:hover {
  background-color: rgb(249, 126, 126);
}

.ai-blue {
  background-color: rgb(165 214 251);
}

.ai-blue:hover {
  background-color: rgb(134, 197, 248);
}

.remirror-editor {
  height: 500px;
}

.remirror-editor p {
  line-height: 1.5rem;
  font-size: 16px;
  margin-bottom: 5px !important;
  letter-spacing: -.025em;
}

.MuiFormLabel-asterisk {
  color: red;
}

.StripeElement {
  box-sizing: border-box;
  height: 40px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  width: 400px;
}

#portal-jobs .header {
  background-color: white;
  padding: 20px;
  text-align: center;
}

#portal-jobs .navbar {
  padding-top: 10px;
  padding-bottom: 10px;
}

#portal-job .apply-form .custom-file-label {
  padding: 10px 35px;
  border-radius: 5px;
  width: 215px !important;
  cursor: pointer;
  background: #ebecf0;
  color: #4f4f51;
  border: 1px solid #dfe0e4;
  position: relative;
}

#portal-job .apply-form .custom-file-label svg {
  position: absolute;
  height: 20px;
  left: 5px;
  top: 15px;
}

#portal-job .apply-form #resume {
  display: none;
}

#portal-job .apply-form .MuiFormLabel-asterisk {
  color: red;
}

#portal-job .apply-form .MuiFormLabel-colorPrimary {
  color: inherit;
}

.blue-link {
  color: rgb(86, 130, 198);
  font-weight: 700;
  font-size: 14px;
  text-decoration: none;
}

#observe-job {
  margin: 40px;
}

#observer-job-detail {
  width: 30%;
  float: left;
  border-right: 1px solid #ccc;
  padding-right: 40px;
}

#observer-job-candidates {
  width: 68%;
  float: right;
}